<template>
  <div class="user-list">
    <h2>Áî®Êà∑ÁÆ°ÁêÜ</h2>

    <!-- Ê∑ªÂä†Áî®Êà∑Ë°®Âçï -->
    <div class="add-user-form">
      <input v-model="newUserName" placeholder="ËæìÂÖ•Áî®Êà∑Âêç" />
      <button @click="addUser">Ê∑ªÂä†Áî®Êà∑</button>
    </div>

    <!-- Áî®Êà∑ÂàóË°® -->
    <div class="user-table">
      <table>
        <thead>
          <tr>
            <th>ID</th>
            <th>ÂßìÂêç</th>
            <th>Êìç‰Ωú</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="user in users" :key="user.id">
            <td>{{ user.id }}</td>
            <td>{{ user.name }}</td>
            <td>
              <button @click="deleteUser(user.id)">Âà†Èô§</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: 'UserList',
  data() {
    return {
      users: [],
      newUserName: ''
    }
  },
  async mounted() {
    await this.loadUsers()
  },
  methods: {
    async loadUsers() {
      try {
        const response = await this.$http.get('/users')
        this.users = response.data
      } catch (error) {
        console.error('Âä†ËΩΩÁî®Êà∑Â§±Ë¥•:', error)
      }
    },

    async addUser() {
      if (!this.newUserName.trim()) return

      try {
        await this.$http.post('/users', {
          name: this.newUserName
        })
        this.newUserName = ''
        await this.loadUsers() // ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑ÂàóË°®
      } catch (error) {
        console.error('Ê∑ªÂä†Áî®Êà∑Â§±Ë¥•:', error)
      }
    },

    async deleteUser(id) {
      try {
        await this.$http.delete(`/users/${id}`)
        await this.loadUsers() // ÈáçÊñ∞Âä†ËΩΩÁî®Êà∑ÂàóË°®
      } catch (error) {
        console.error('Âà†Èô§Áî®Êà∑Â§±Ë¥•:', error)
      }
    }
  }
}
</script>

<style scoped>
.user-list {
  padding: 20px;
}

.add-user-form {
  margin: 20px 0;
}

.add-user-form input {
  padding: 8px;
  margin-right: 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.add-user-form button {
  padding: 8px 16px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.user-table table {
  width: 100%;
  border-collapse: collapse;
}

.user-table th,
.user-table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.user-table th {
  background-color: #f2f2f2;
}

button {
  padding: 6px 12px;
  background-color: #f44336;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  opacity: 0.8;
}
</style>