<template>
  <div class="dashboard-container">
    <!-- ÁªüËÆ°Âç°Áâá -->
    <el-row :gutter="20">
      <el-col :span="6">
        <el-card shadow="hover" class="stats-card">
          <div class="card-content">
            <div class="card-icon user-icon">
              <el-icon><User /></el-icon>
            </div>
            <div class="card-info">
              <div class="card-title">ÊÄªÁî®Êà∑Êï∞</div>
              <div class="card-value">{{ userCount }}</div>
            </div>
          </div>
        </el-card>
      </el-col>
      <el-col :span="6">
        <el-card shadow="hover" class="stats-card">
          <div class="card-content">
            <div class="card-icon growth-icon">
              <el-icon><TrendCharts /></el-icon>
            </div>
            <div class="card-info">
              <div class="card-title">‰ªäÊó•Â¢ûÈïø</div>
              <div class="card-value">+{{ todayGrowth }}</div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>

    <!-- Áî®Êà∑Ë°®Ê†º -->
    <el-card class="table-card">
      <template #header>
        <div class="card-header">
          <span>Áî®Êà∑ÂàóË°®</span>
          <el-button type="primary" @click="fetchUsers">Âà∑Êñ∞</el-button>
        </div>
      </template>
      <el-table :data="users" style="width: 100%" v-loading="loading">
        <el-table-column prop="id" label="ID" width="80"></el-table-column>
        <el-table-column prop="name" label="ÂßìÂêç"></el-table-column>
        <el-table-column label="Êìç‰Ωú">
          <template #default="scope">
            <el-button size="small" @click="handleEdit(scope.row)">ÁºñËæë</el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.row)">Âà†Èô§</el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- Ê∑ªÂä†Áî®Êà∑ÂØπËØùÊ°Ü -->
    <el-dialog v-model="dialogVisible" title="Ê∑ªÂä†Áî®Êà∑" width="30%">
      <el-form :model="newUser" label-width="80px">
        <el-form-item label="ÂßìÂêç">
          <el-input v-model="newUser.name" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false">ÂèñÊ∂à</el-button>
          <el-button type="primary" @click="addUser">Á°ÆËÆ§</el-button>
        </span>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { ref, onMounted } from 'vue'
import { ElMessage } from 'element-plus'

export default {
  name: 'DashboardView',
  setup() {
    const users = ref([])
    const loading = ref(false)
    const dialogVisible = ref(false)
    const newUser = ref({ name: '' })
    const userCount = ref(0)
    const todayGrowth = ref(5)

    const fetchUsers = async () => {
      loading.value = true
      try {
        const response = await fetch('/api/users')
        users.value = await response.json()
        userCount.value = users.value.length
      } catch (error) {
        ElMessage.error('Ëé∑ÂèñÁî®Êà∑Êï∞ÊçÆÂ§±Ë¥•')
      } finally {
        loading.value = false
      }
    }

    const addUser = async () => {
      if (!newUser.value.name.trim()) {
        ElMessage.warning('ËØ∑ËæìÂÖ•Áî®Êà∑ÂßìÂêç')
        return
      }

      try {
        const response = await fetch('/api/users', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify(newUser.value)
        })

        if (response.ok) {
          ElMessage.success('Áî®Êà∑Ê∑ªÂä†ÊàêÂäü')
          dialogVisible.value = false
          newUser.value.name = ''
          fetchUsers()
        }
      } catch (error) {
        ElMessage.error('Ê∑ªÂä†Áî®Êà∑Â§±Ë¥•')
      }
    }

    const handleEdit = (user) => {
      ElMessage.info(`ÁºñËæëÁî®Êà∑: ${user.name}`)
    }

    const handleDelete = async (user) => {
      try {
        const response = await fetch(`/api/users/${user.id}`, {
          method: 'DELETE'
        })

        if (response.ok) {
          ElMessage.success('Áî®Êà∑Âà†Èô§ÊàêÂäü')
          fetchUsers()
        }
      } catch (error) {
        ElMessage.error('Âà†Èô§Áî®Êà∑Â§±Ë¥•')
      }
    }

    onMounted(() => {
      fetchUsers()
    })

    return {
      users,
      loading,
      dialogVisible,
      newUser,
      userCount,
      todayGrowth,
      fetchUsers,
      addUser,
      handleEdit,
      handleDelete
    }
  }
}
</script>

<style scoped>
.dashboard-container {
  padding: 20px;
}

.stats-card {
  margin-bottom: 20px;
}

.card-content {
  display: flex;
  align-items: center;
}

.card-icon {
  width: 60px;
  height: 60px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 15px;
  font-size: 24px;
}

.user-icon {
  background: #ecf5ff;
  color: #409eff;
}

.growth-icon {
  background: #f0f9eb;
  color: #67c23a;
}

.card-info {
  flex: 1;
}

.card-title {
  font-size: 14px;
  color: #909399;
  margin-bottom: 5px;
}

.card-value {
  font-size: 24px;
  font-weight: bold;
  color: #303133;
}

.table-card {
  margin-top: 20px;
}

.card-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>